        fun() {
            array = [...this.tableData];
        },
        sf() {
            this.tableData = array.filter((item) => {
                return item;
            });
        },
        zhong() {
            this.tableData = array.filter((item) => {
                return item;
            });
        },
        sfjf() {
            this.tableData = array.filter((item) => {
                return item;
            });
